@import '../../assets/stylesheets/base/variables';
@import '../../assets/stylesheets/base/mixins';
@import '../../assets/stylesheets/base/breakpoints';

@import '../../assets/stylesheets/shared/animations';
@import '../../assets/stylesheets/shared/base';
@import '../../assets/stylesheets/shared/buttons';
@import '../../assets/stylesheets/shared/fonts';
@import '../../assets/stylesheets/shared/forms';
@import '../../assets/stylesheets/shared/utilities';

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 1000px;
    padding: $m-sm;

    &__menu {
        display: flex;
        flex-direction: column;
        align-items: center;

        &-list {
            justify-content: center;
            align-items: center;
            display: flex;
            flex-direction: column;
            max-height: 0;
            opacity: 0;
            transition: opacity .5s;

            &.-opened {
                opacity: 1;
                max-height: 500px; } } }

    button {
        margin: $m-sm; }

    &__icon {
        padding-top: $m-md;
        height: 100%;
        width: 90px; }

    &__hamburger {
        align-self: flex-end;
        transition: .5s;
        width: 40px;
        height: 40px;
        outline:none {}
        background-color: transparent;
        background-repeat:no-repeat {}
        border: none;
        overflow: hidden;

        &:focus {
            outline: none; }

        &.-opened {
            transform: rotate(90deg); } }

    &__link {
        color: $gray-40;
        font-size: $font-size-sm;
        font-weight: $font-weight-bold;
        margin: $m-sm;
        text-decoration: none; } }

@include media-breakpoint-up(md) {
    .header {
        &__menu-list {
            flex-direction: row;
            max-height: 100px;
            opacity: 1; }

        &__icon {
            align-self: center;
            margin-right: auto;
            padding-top: 0; }

        &__hamburger {
            display: none; } } }

@include media-breakpoint-up(xl) {
    .header {
        padding: $m-sm 0; } }
