* {
  box-sizing: border-box; }

body {
  color: $text-color;
  font: {
    family: $font-stack-body;
    size: $font-size-base; }
  line-height: $line-height-base;
  margin: 0; }

#root {
  display: flex;
  flex-direction: column;
  align-items: center; }

a,
button,
input[type='button'] {
  cursor: pointer; }

img {
  display: block;
  max-width: 100%; }
